import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lang: "nl",
    country: "nl",
  },
  mutations: {
    setLocales(state, newLang) {
      state.lang = newLang;
      state.country = newLang === "nl" ? newLang : "be";
    },
  },
  actions: {

  },
});
